<script>
export default {
  name: 'RouteView',
  data () {
    return {}
  },
  render () {
    const { $route: { meta }, $store: { getters } } = this

    const inKeep = (
      <keep-alive>
        <router-view />
      </keep-alive>
    )
    const notKeep = (
      <router-view />
    )
    //  multiTab  multiTab 
    // 
    //  return meta.keepAlive ? inKeep : notKeep
    return meta.keepAlive || getters.multiTab ? inKeep : notKeep
  }
}
</script>
